<template>
	<el-container class="wrap">
		<el-header style="height: auto"><app-header></app-header></el-header>
		<el-container class="content">
		 	<el-main>
			    <router-view></router-view>			   	
		 	</el-main>
		 	<el-aside>
		 		Aside 
		 		<br/>先占个广告位吧
		 	</el-aside>
		</el-container>
		<el-footer style="height: auto"><app-footer></app-footer></el-footer>
  </el-container>
</template>

<script>
import appHeader from '@/views/public/Header';
import appFooter from '@/views/public/Footer';
export default {
  name: "TheIndex",
  components: {appHeader, appFooter},
};
</script>

<style lang="scss" scoped>
	@import '@/assets/public/css/box.scss';
	.el-header{
		@include main-header;
	}
	.el-main {
		@include main-body;
	}
	.el-aside{
		@include main-aside;
	}
	.el-footer{
		@include main-footer;
	}
</style>



