<template>
	<div v-if="item.meta" class="aside-item">
		<template v-if="!item.children || item.children.length == 0">
			<el-menu-item :index="'/admin/' + item.path">
				<template slot="title">{{ item.meta.title }}</template>
			</el-menu-item>
		</template>
		<template v-else>  	
	    	<el-submenu :index="item.path">
	    		<template slot="title">{{ item.meta.title }}</template>
				<aside-item
				  v-for="child in item.children"
				  :item="child"
				  :key="child.path"
				>
				</aside-item>
			</el-submenu>
		</template>
	</div>
</template>

<script>
export default {
  name: 'AsideItem',
  props:{
  	item: {
      type: Object,
      required: true
    },
  },
	data(){
		if(this.item.meta)
			console.log(this.item)
		return {}
	}
}
</script>

<style>
</style>


