const constantRouterMap = []

const asyncRouterMap = [
  {
    path: '/admin',
    name: 'admin',
    component: () => import('@/views/admin/TheAdmin'),
    meta: {
      title: '管理',
      icon: '',
      roles: {
        'admin': 5
      }
    },
    children: [
      {
        path: 'main',
        name: 'Main',
        component: () => import('@/views/admin/Main'),
        meta: {
          title: '综合设置',
          icon: '',
          roles: {
            'admin': 1
          }
        }
      },
      {
        path: 'login',
        name: 'login',
        component: () => import('@/views/admin/main/Login'),
        meta: {
          title: '登录设置',
          icon: '',
          roles: {
            'admin': 1
          }
        }
      },
      {
        path: 'review',
        name: 'Test1',
        component: () => import('@/views/admin/Review'),
        meta: {
          title: '测试1',
          icon: '',
          roles: {
            'admin': 5
          }
        },
      },]
  }]

export {constantRouterMap as adminC, asyncRouterMap as adminA}
  


