import { getToken, setToken, removeToken } from '@/utils/auth'
import userApi from '@/api/user/index'
const user = {
  state: {
    token: getToken(),
    id: 0,
    name: '',
    avatar: '',
    roles: {}
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
      setToken(token)
      sessionStorage.setItem('token',token)
    },
    SET_NAME: (state, name) => {
      state.name = name
      sessionStorage.setItem('name',name)
    },
    SET_AVATAR: (state, avatar) => {
      state.avatar = avatar
      sessionStorage.setItem('avatar',avatar)
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles
      sessionStorage.setItem('roles',JSON.stringify(roles))
    },
    SET_ID: (state, id) => {
      state.id = id
      sessionStorage.setItem('id',id)
    }
  },

  actions: {
    // 登录
    LoginAfter({ commit }, res) {
  		setToken(res.token)
      commit('SET_TOKEN', res.token)
      commit('SET_ROLES', res.user.roles)
      commit('SET_NAME', res.user.name)
      commit('SET_AVATAR', res.user.avatar)
    },

    // 获取用户信息
    GetUserInfo({ commit, state }) {
      return new Promise (async(resolve, reject) => {
      	try{
	      	let res = await userApi.getUserInfo({token: state.token})
	      	if (res.status == 2000) { 
            commit('SET_ROLES', res.user.roles)
            commit('SET_NAME', res.user.name)
          	commit('SET_AVATAR', res.user.avatar)
          	commit('SET_ID', res.user.id)
          	resolve(res.user)
         }
      	}
      	catch(error){
      		console.log(error)
      		reject(error)
      	}
      })
    },

    // 登出
    LogoutBefore({ commit }) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '')
        commit('SET_ROLES', {})
        removeToken()
        sessionStorage.clear() 
        resolve()
      })

    },

    // 前端 登出
    FedLogOut({ commit }) {
      return new Promise(resolve => {
        commit('SET_TOKEN', '')
        removeToken()
        resolve()
      })
    }
  }
}

export default user



