import Theindex from '@/views/index/TheIndex'
import main from '@/views/index/Index'

const constantRouterMap = [{
  path: '/index',
  name: 'index',
  component: Theindex,
  children: [{
    path: 'main',
    name: 'main',
    component: main,
  }]
}]

const asyncRouterMap = []

export {constantRouterMap as indexC, asyncRouterMap as indexA}



