import api from '../index'
import urls from './urls'


export default {
  getItems (params) {
    return api.get(urls.getItems, params)
  },
  getArticle (params) {
  	return api.get(urls.getArticle, params)
  },
  getModuleInfo (params) {
    return api.get(urls.getModuleInfo, params)
  },
  likeArticle (params) {
    return api.post(urls.likeArticle, params)
  },
}


