<template>	
	<el-row type="flex" justify="start" class="main">
		<el-card :body-style="{ padding: '10px' }"  class="module" v-for="module in module" :key="module.id">
			<el-row type="flex" justify="start">
				<el-col :span="4" 
					style="text-align: center;">	
					<router-link :to="module.href">
						<img :src="module.avatar || require('../../assets/public/images/logo.png')"/>
					</router-link>
				</el-col>
				<el-col class="module-concent" :span="20">
					<el-col :span="6">
						<router-link :to="module.href">
							<span class="module-title">{{ module.title }}</span>
						</router-link>
					</el-col>
					<el-col :span="12">
						<span class="module-description">{{ module.description }}</span>
					</el-col>
					<el-col :span="6">
						<div class="module-info">
							<div class="module-time">{{ module.updateTime }}</div>
							<div class="module-count">{{ module.count }}条内容</div>
						</div>
					</el-col>
				</el-col>
			</el-row>
		</el-card>
	</el-row>
</template>

<script>
	export default {
		name: 'index',
		data(){
			return {
				module: [],
			}
		},
		created(){
			(async () => {
				try{
					let res = await this.$api.index.getModuleItems();
					if(res.status === 2000){
						this.module = res.module;
					}else{
						this.$message.error('服务器繁忙');
					}
				}catch(e){
					this.$message.error('服务器繁忙');
					console.log(e);
				}
			})()
		}
	}
</script>

<style scoped lang="scss">
	@import '@/assets/public/css/cross-browser.scss';
	@import '@/assets/public/css/box.scss';
	@import '@/assets/public/css/font.scss';
	.main{
		flex-flow: column wrap;
		.module{
			@include index-main-box;
			margin-bottom: 20px;
			img{
				height: 150px;
			}
			.module-title{
				@include title-font;
			}
			.module-description{
				@include description-font;
				padding-right: 40px;
			}
			.module-info{
				@include info-font;
				div{
					margin-bottom: 10px;
				}
			}
			.module-concent{
				text-align: left;
				> * {
					padding: 10px;
					height: 100%;
					border-left: 2px solid rgba(30,30,30,.2);		
				}
				
			}
		}
	}
</style>


