import theLogin from '@/views/user/login/TheLogin'
import Login from '@/views/user/login/Login'
const constantRouterMap = [{
  path: '/user',
  name: 'user',
  component: theLogin,
  children:[{
    path: 'login',
    name: 'login',
    component: Login
  },{
    path: 'register',
    name: 'register',
    component: () => import ('@/views/user/login/Register')  
  },]
},
  {
    path: '/editor',
    name: 'editor',
    component: () => import ('@/views/user/editor/TheEditor')
  }]

const asyncRouterMap = []

export {constantRouterMap as userC, asyncRouterMap as userA}



