const getters = {
  sidebar: state => state.app.sidebar,
  language: state => state.app.language,
  size: state => state.app.size,
  device: state => state.app.device,
  token: state => sessionStorage.getItem('token') || state.user.token,
  avatar: state => sessionStorage.getItem('avatar') || state.user.avatar,
  name: state => sessionStorage.getItem('name') || state.user.name,
  introduction: state => state.user.introduction,
  roles: state => JSON.parse(sessionStorage.getItem('roles')) || state.user.roles,
  realRoles: state =>  state.user.roles,
  setting: state => state.user.setting,
  permission_routers: state => state.permission.routers,
  addRouters: state => state.permission.addRouters,
}
export default getters



