<template>
	<el-row class="header">
		<el-col :span="24">
			<el-menu :default-active="$route.path" mode="horizontal" router
				style="font-size: 20px;">
				<div class="logoInfo">
					<router-link to="/index/main">
						<img src="@/assets/public/images/logo.png" />
						<span>{{ sysName }}</span>
					</router-link >
				</div>
				<aside-item class="tal" 
					v-if="routerShow"
					v-for="route in permission_routers" :key="route.path" 
					:item="route" >
				</aside-item>
				
				<div class="userinfo">
					<span class="function"><el-button type="primary" icon="el-icon-edit" @click.native="write">写博客</el-button></span>
					<el-dropdown trigger="hover">
						<div class="el-dropdown-link userinfo-inner">
							<span class="large">{{userName}}</span>
							<img :src="userAvatar" /> 
						</div>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item>个人中心</el-dropdown-item>
							<el-dropdown-item>我的消息</el-dropdown-item>
							<el-dropdown-item>设置</el-dropdown-item>
							<el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
							<el-dropdown-item v-if="$store.getters.roles['admin']">
								<router-link to="/admin"><span  class="green bold">我的管理</span></router-link>
							</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>
			</el-menu>
		</el-col>
	</el-row>
</template>

<script>
	import asideItem from '@/views/public/AsideItem'
	import { mapGetters } from 'vuex'
	export default {
		name: 'AppHeader',
		components: { asideItem },
		props: {routerShow:{
	      type: Boolean,
	      default: false,
	    }},
		computed: {
	    	...mapGetters([
	      		'permission_routers'
	    	]),
	  	},
		data() {
			console.log(this.$store.getters.roles['admin'])
			return {
				sysName: 'San-greal 圣杯网',
				userName: this.$store.getters.name || '无用户名',
				userAvatar: this.$store.getters.avatar || require('../../assets/public/images/user.jpg'),
			}
		},
		methods: {
			logout(){
				var _this = this;
				this.$confirm('确认退出吗?', '提示', {
					type: 'warning'
				}).then(async () => {
					try{
						let res = await this.$api.user.logout({token: this.$store.getters.token});
						if(res.status === 2000){
							this.$store.dispatch('LogoutBefore')
							this.$router.push('/user/login')
						}
					}catch(e){
						console.log(e);
					}
				}).catch(() => {
				});
			},
			write(){
				this.$router.push('/editor')
			}
		},
	}
</script>

<style scoped lang="scss">
	@import "@/assets/public/css/cross-browser.scss";
	@import '@/assets/public/css/_colorSettings.scss';
	.header {
		background-color: $header-bg-color;
		height: 60px;
		.el-menu{
			background: transparent;
			.aside-item{
				float: left;
				position: relative;
				line-height: 56px;
				&:after{
					content: "|";
				    position: absolute;
				    top: 0px;
				    right: 0px;
				    color: hsla(0,0%,59.2%,.4);
				    font-size: 30px;
				}
			}
		}
		.userinfo {
			text-align: right;
			padding-right: 10px;
			float: right;
			line-height:56px;
			.userinfo-inner {
				cursor: pointer;
				img {
					width: 40px;
					height: 40px;
					border-radius: 20px;
					margin: 10px 0px 10px 10px;
					float: right;
					@include box-shadow;
				}
			}
		}
	}
	.logoInfo {
		float: left;
		img {
			height: 40px;
			margin: 10px 0px 10px 10px;
		}
		span {
			position: relative;
			left: auto;
			bottom: 25px;
			font-weight: bold;
		}
		a:visited{
			color: black;
		}
	}
	.function{
		margin: 10px 0px 10px 10px;
		.el-button{
			background: #409EFF;
		}
	}
</style>


