const baseUrl = '/user/'

export default {
  login: baseUrl + 'login',
  logout: baseUrl + 'logout',
  register: baseUrl + 'register',
  getUserInfo: baseUrl + 'userInfo',  
  validateAccount: baseUrl + 'register/' + 'validateAccount',
  
  submitArticle: baseUrl + 'article',
  editArticle: baseUrl + 'article',
}


