import router from '@/router'
import { Message } from 'element-ui'
import { getToken, removeToken } from '@/utils/auth' // getToken from cookie

// permission judge function
function hasPermission(roles, permissionRoles) {
	if(permissionRoles){
		for(let key in permissionRoles)
			if(roles[key] && roles[key] >= permissionRoles[key])
				return true
		return false
	}
	else
		return true
}

function routerEach(store, router, whiteList=['/user/login', '/user/register', '/help/404']){
	router.beforeEach((to, from, next) => {
	  console.log('token =' +  getToken())
	  if (getToken()) { // determine if there has token
	    console.log('has token')
		  if (Object.keys(store.getters.realRoles).length  === 0) { // 判断当前用户是否已拉取完user_info信息
		      console.log('GetUserInfo')
		      store.dispatch('GetUserInfo').then(() => {
		        console.log('GetUserInfo success')
            let roles = store.getters.roles;
            store.dispatch('GenerateRoutes', { roles }).then(() => { // 根据roles权限生成可访问的路由表
              router.addRoutes(store.getters.addRouters) // 动态添加可访问路由表
              next({ ...to, replace: true }) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
            })
		      }).catch((e)=>{
		        console.info('用户信息得到失败')
		        console.log(e)
		        removeToken();
		        next('/user/login')
		      })
		  } else {
		    // 没有动态改变权限的需求可直接next() 删除下方权限判断 ↓
		    if (hasPermission(store.getters.roles, to.meta.roles)) {
		      next()
		    } else {
		      next('/help/404') 
		    }
		    // 可删 ↑
		  }
	  } else {
	    /* has no token*/
	   console.info('no token')
	    if (whiteList.indexOf(to.path) !== -1) { // 在免登录白名单，直接进入
	      console.info('免登陆进入')
	      next()
	    } else {
	      next('/user/login')// 否则全部重定向到登录页
	    }
	  }
	})
}

export default routerEach





