<template>
	<el-col :span="8" :pull="3" class="login" 
	:xm="20">
		<div id="login">
			<el-header>
				<el-row>
					<h2>欢迎登录</h2>
				</el-row>
			</el-header>
			<el-main>
				<el-form :model="loginForm" ref="loginForm" label-width="80px" @keyup.enter.native="submitForm('loginForm')">
					<el-form-item label="账号:" prop="account"
						 :rules="{required: true, message: '账号不能为空', trigger: 'blur'}">
						<el-input v-model.trim="loginForm.account" placeholder="输入 账号/用户名/邮箱"></el-input>
					</el-form-item>
					<el-form-item label="密码:" prop="pwd"
						 :rules="{required: true, message: '密码不能为空', trigger: 'blur'}">
						<el-input type="password" v-model="loginForm.pwd" placeholder="输入 密码" autocomplete="off"></el-input>
					</el-form-item>
					<el-form-item prop="verifycode" v-if="verify" label="验证码:" :rules="verifycode" autocomplete="off" class="identify">
					  	<el-input v-model="loginForm.verifycode" placeholder="输入验证码"></el-input>
						<div class="pointer fr" @click="refreshCode">
							<s-identify :identifyCode="identifyCode" :contentWidth="80"></s-identify>
						</div>	
					</el-form-item>
					<el-form-item 
						style="margin-top: -12px">
						<el-checkbox v-model="loginForm.autoLogin" class="small fl">自动登录</el-checkbox>
						<span class="small red fr">{{ message }}</span>
					</el-form-item>
					<el-form-item>				
						<el-button type="primary" @click="submitForm('loginForm')" :loading="loading"
							style="width:100%">
							登 录
						</el-button>
					</el-form-item>
				</el-form>
				<div class="dividing_01"></div>
				<el-row>
					<div class="fr small">
						<a href="#">忘记密码</a>
						<router-link to="/user/register">立即注册-></router-link>
					</div>
				</el-row>
			</el-main>
		</div>
	</el-col>
</template>

<script>
	import SIdentify from './SIdentify.vue';
	import { setToken, getToken } from '@/utils/auth.js'
	export default {
		name: 'login',
		data() {
			const validateVerifycode = (rule, value, callback) => {
				if(!value) {
					callback(new Error('请输入验证码'))
				} else if(value !== this.identifyCode) {
					callback(new Error('验证码不正确!'))
				} else {
					callback()
				}
			};
			return {
				verify: false,
				identifyCodes: '1234567890',
				identifyCode: '',
				message: '',
				loading: false,
				loginForm: {
					account: sessionStorage.getItem('account')? sessionStorage.getItem('account'):'',
					pwd: '',
					autoLogin: true,
//					verifycode: '',
				},
				verifycode: [{
					validator: validateVerifycode,
					trigger: 'blur'
				}]
			};
		},
		methods: {
			 submitForm(formName) {
				this.$refs[formName].validate( async(valid) => {
					try{
						if(valid){
							this.loading = true;
							let res = await this.$api.user.login(this.loginForm);
							if(res.status == 2000){
								this.$store.dispatch('LoginAfter', res);
								this.$store.dispatch('GenerateRoutes', {roles: res.user.roles} ).then(() => { // 根据roles权限生成可访问的路由表
					              this.$router.addRoutes(this.$store.getters.addRouters) // 动态添加可访问路由表
					            })
								this.$router.push('/index/main');
							}
							else
								this.message = res.message;
						}
					}
					catch(e){
						console.log(e);
						this.$message.error('服务器繁忙');
					}
					this.loading = false;
				});
			},
			// 生成随机数
			randomNum(min, max) {
				return Math.floor(Math.random() * (max - min) + min)
			},
			// 切换验证码
			refreshCode() {
				this.identifyCode = ''
				this.makeCode(this.identifyCodes, 4)
			},
			// 生成四位随机验证码
			makeCode(o, l) {
				for(let i = 0; i < l; i++) {
					this.identifyCode += this.identifyCodes[this.randomNum(0, this.identifyCodes.length)]
				}
			}
		},
		mounted(){
			// 验证码初始化
			this.makeCode(this.identifyCodes, 4)
		},
		components: {
		 SIdentify
		},
	}
</script>

<style scoped  lang="scss">
	div a {
		margin-left: 10px;
	}
	.iconstyle {
		color: #409EFF;
	}
	.identify .el-input{
		width:110px;
	}
	.inline > *{
		display: inline-block;
	}
</style>


