import api from '../index'
import urls from './urls'


export default {
  login (params) {
  	return api.post(urls.login, params)
  },
  logout (params) {
    return api.post(urls.logout, params)
  },
  register (params) {
    return api.post(urls.register, params)
  },  
  validateAccount (params) {
    return api.get(urls.validateAccount, params)
  },
  getUserInfo (params) {
    return api.get(urls.getUserInfo, params)
  },
  
  submitArticle(params) {
    return api.post(urls.submitArticle, params)
  },
  editArticle(params) {
    return api.put(urls.editArticle, params)
  },
}


