<template>
  <el-container class="wrap">
  	<el-main>
  		<el-row
  			style="height: 20vh;">
	    	<!--<img src="@/assets/logo.png"/>-->
	    </el-row>
	    <el-row type="flex" justify="end">
	   			<router-view></router-view>
	   	</el-row>
   	</el-main>
   	<el-footer style="height: auto;"><app-footer></app-footer></el-footer>
  </el-container>
</template>

<script>
import appFooter from '@/views/public/Footer'
//import myLogin from './Login'
	export default {
	  name: 'App',
	  components:{appFooter},
	}
</script>

<style scoped lang="scss">
@import '@/assets/public/css/cross-browser.scss';
@import '@/assets/public/css/box.scss';
	.wrap {
		color: #2c3e50;
		text-align: center;
		height: 100vh;
		min-height: 675px;
		background: url('../../../assets/images/loginBg.jpg') 50% 50% no-repeat;
		background-size: cover;
	}
	.login{
		background: white;
		padding: 30px 40px 0px 30px;
		@include box-shadow(0px, 10px, 30px, rgba(0, 0, 0, .1)); 
	}
	.el-footer{
		@include main-footer;
	}
</style>



