import Vue from 'vue'
import Router from 'vue-router'
import {userC, userA}  from './modules/user' 
import {adminC, adminA}  from './modules/admin' 
import {webC, webA}  from './modules/web' 
import {indexC, indexA}  from './modules/index' 
Vue.use(Router)

export const constantRouterMap = [
  ...userC,
  ...indexC,
  ...webC,
  ...adminC,
  {
    path: '/',
    redirect: '/index/main'
  },
]

export const asyncRouterMap = [
  ...userA,
  ...indexA,
  ...webA,
  ...adminA,
  {
    path: '/*',
    redirect: '/index/main'
  },
]

export default new Router({
  routes: constantRouterMap,
})




