const modules = [
  {
    id: 100,
    name: 'web',
    title: '前端之路',
    children:[
    {
      id: 101,
      name: 'javascprit',
    },
     {
      id: 102,
      name: 'css',
    },
    {
      id: 103,
      name: 'html',
    }]
  }]

function getModules(id){
  for(let i=0,len=modules.length; i<len; i++){
    if(id == modules[i].id)
      return modules[i].children
    return null
  }
}

function getModuleId(name){
  for(let i=0,len=modules.length; i<len; i++){
    if(name == modules[i].name)
      return modules[i].id
    return null
  }
}

export {getModules, getModuleId}


