import { getModuleId } from '@/assets/appModules/modules'
const constantRouterMap = [
  {
    path: '/web',
    name: 'web',
    component: () => import('@/views/web/TheWeb')
  },
  {
    path: '/web/:id',
    name: 'article',
    component: () => import('@/views/web/article/TheArticle'),
  },
  {
    path: '/web/:id/edit',
    name: 'edit',
    component: () => import('@/views/user/editor/TheEditor'),
  }
]

const asyncRouterMap = []

export {constantRouterMap as webC, asyncRouterMap as webA}



